import Calendar from "react-calendar"

import DashboardBtn from "../UI/dashboardBtn"

import styles from "../styles/Dashboard.module.scss"
import "react-calendar/dist/Calendar.css"

import logo from "../Images/dashboard/logo.svg"
import devider from "../Images/dashboard/devider.svg"
import iconPerson from "../Images/dashboard/icon-person.svg"
import metro from "../Images/dashboard/icon-metro.svg"
import trolleybus from "../Images/dashboard/trolleybus_white 1.svg"
import tram from "../Images/dashboard/material-symbols_tram.svg"
import help from "../Images/dashboard/bx_support.svg"
import exit from "../Images/dashboard/exit.svg"
import user from "../Images/dashboard/user_photo_small.svg"
import person from "../Images/dashboard/user_photo_full.jpg"
import line from "../Images/dashboard/line.png"
import discord from "../Images/dashboard/discord.svg"
import bin from "../Images/dashboard/bin.svg"
import lines from "../Images/bgLines.png"

import { useState } from "react"
import { NavLink } from "react-router-dom"

import HeaderLk from "../layout/HeaderLk"

function Dashboard() {
  const [activity, changeActivity] = useState(0)

  const [value, setValue] = useState(new Date())

  function onChange(nextValue) {
    setValue(nextValue)
  }

  return (
    <>
      <div className={styles.lk}>
        <HeaderLk />
      </div>
      <div className={styles.dashboard}>
        <img
          src={lines}
          className={styles.line}
        />
        <div className={styles.left}>
          <div className={styles.top}>
            <img src={logo} />
            <h2>Sunrise</h2>
            <img src={devider} />
            <p> , username123</p>
            <div className={styles.menu}>
              <p></p>
              <button onClick={() => changeActivity(0)}>
                <DashboardBtn
                  img={iconPerson}
                  title=''
                />
              </button>
            </div>
            <div className={styles.menu}>
              <p> </p>
              <button onClick={() => changeActivity(1)}>
                <DashboardBtn
                  img={metro}
                  title=''
                />
              </button>
              <button onClick={() => changeActivity(0)}>
                <DashboardBtn
                  img={trolleybus}
                  title=''
                />
              </button>
              <button onClick={() => changeActivity(0)}>
                <DashboardBtn
                  img={tram}
                  title=''
                />
              </button>
            </div>
          </div>
          <div className={styles.bottom}>
            <NavLink to=''>
              <DashboardBtn
                img={help}
                title=''
              />
            </NavLink>
            <NavLink to='/sunrise/login'>
              <DashboardBtn
                img={exit}
                title=''
              />
            </NavLink>
          </div>
        </div>
        <div className={styles.right}>
          {activity === 0 && (
            <>
              <div className={styles.top}>
                <div className={styles.wrapper}>
                  <div className={styles.text}>
                    <h3> </h3>
                    <p>     Sunrise!</p>
                  </div>
                  <div className={styles.user}>
                    <img src={user} />
                    <p> </p>
                  </div>
                </div>
              </div>

              <div className={styles.main}>
                <div className={styles.info}>
                  <div className={styles.element}>
                    <img src={person} />
                    <div className={styles.discord}>
                      <div className={styles.wrapper}>
                        <h3> Discord:</h3>
                        <div className={styles.account}>
                          <img src={discord} />
                          <div className={styles.username}>
                            <p> : @UserName</p>
                            <span> </span>
                          </div>
                        </div>
                      </div>
                      <div className={styles.change}>
                        <div className={styles.bin}>
                          <img src={bin} />
                        </div>
                        <button>  </button>
                      </div>
                    </div>
                  </div>
                  <div className={styles.data}>
                    <h4> :</h4>
                    <form>
                      <div className={styles.name}>
                        <input
                          type='text'
                          placeholder=''
                        ></input>
                        <input
                          type='text'
                          placeholder=''
                        ></input>
                      </div>
                      <input
                        type='mail'
                        placeholder='example@gmail.com'
                      ></input>
                      <input
                        type='text'
                        placeholder='username123'
                      ></input>
                      <button> </button>
                    </form>
                  </div>
                </div>
                <div className={styles.second}>
                  <div className={styles.discord}>
                    <img
                      src={line}
                      className={styles.divider}
                    />
                    <div className={styles.wrapper}>
                      <h3> Discord:</h3>
                      <div className={styles.account}>
                        <img src={discord} />
                        <div className={styles.username}>
                          <p> : @UserName</p>
                          <span> </span>
                        </div>
                        <img src={bin} />
                      </div>
                    </div>
                  </div>
                  <div className={styles.events}>
                    <img src={line} />
                    <div className={styles.wrapper}>
                      <h3>,    :</h3>
                      <p>
                               
                      </p>
                    </div>
                  </div>
                  <div className={styles.about}>
                    <img src={line} />
                    <div className={styles.wrapper}>
                      <h3>  :</h3>
                      <div className={styles.blocks}>
                        <div className={styles.block}>
                          <h5> :</h5>
                          <p>username123</p>
                        </div>
                        <div className={styles.block}>
                          <h5> ID:</h5>
                          <p>0000001</p>
                        </div>
                        <div className={styles.block}>
                          <h5>  :</h5>
                          <p>10 </p>
                        </div>
                        <div className={styles.block}>
                          <h5>:</h5>
                          <p></p>
                        </div>
                        <div className={styles.block}>
                          <h5> :</h5>
                          <span> </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
          )}
          {activity === 1 && (
            <>
              <div className={styles.top}>
                <div className={styles.wrapper}>
                  <div className={styles.text}>
                    <h3></h3>
                    <p>     -</p>
                  </div>
                  <div className={styles.user}>
                    <img src={user} />
                    <p> </p>
                  </div>
                </div>
              </div>

              <div className={styles.calendar}>
                <h3> :</h3>
                <div className={styles.wrapper}>
                  <Calendar
                    onChange={onChange}
                    value={value}
                    className={styles.cal}
                  />
                  <div className={styles.booking}>
                    <h4> </h4>
                    <span>  </span>
                    <p>
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                      sed do eiusmod tempor incididunt ut labore. Lorem ipsum
                      dolor sit amet, consectetur adipiscing elit, sed do
                      eiusmod tempor incididunt ut labore Lorem ipsum dolor sit
                      amet, consectetur adipiscing elit, sed do eiusmod tempor
                      incididunt ut labore{" "}
                    </p>
                    <button>  </button>
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
      </div>
    </>
  )
}

export default Dashboard
