import styles from "../styles/Login.module.scss"

import logo from "../Images/login/logo.svg"
import image from "../Images/account/image.jpg"
import steam from "../Images/account/steam.svg"
import lines from "../Images/bgLines.png"
import burger from "../Images/burger.png"
import photo from "../Images/login/image.jpg"

import { NavLink } from "react-router-dom"
import { useState } from "react"

function Login() {
  const [showBurger, setShowBurger] = useState(false)

  return (
    <div className={styles.login}>
      <img
        src={lines}
        className={styles.lines}
      />
      <div className={styles.left}>
        <div className={styles.top}>
          <img src={logo} />
          <NavLink to='/sunrise/'></NavLink>
          <NavLink to='/sunrise/contacts'></NavLink>
          <button
            className={styles.burgerMenuButton}
            onClick={() => setShowBurger((prev) => !prev)}
          >
            <img src={burger}></img>
          </button>
          {showBurger && (
            <>
              <div
                className={styles.burgerMenuWrapper}
                onClick={() => setShowBurger(false)}
              ></div>
              <div className={styles.burgerMenu}>
                <nav>
                  <NavLink
                    className={styles.link}
                    to='project'
                  >
                     
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='team'
                  >
                    
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='jobs'
                  >
                      
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='events'
                  >
                    
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='documents'
                  >
                    
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='contacts'
                  >
                    
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='login'
                  >
                     
                  </NavLink>
                </nav>
              </div>
            </>
          )}
        </div>
        <div className={styles.adaptive}>
          <img src={photo} />
        </div>
        <div className={styles.main}>
          <h5>   </h5>
          <h3>  !</h3>
          <p>
              ?{" "}
            <NavLink to='/sunrise/account'></NavLink>
          </p>
          <form>
            <div className={styles.block}>
              <label for='login'></label>
              <input
                type='login'
                id='login'
                placeholder='username123'
              ></input>
            </div>
            <div className={styles.block}>
              <label for='password'></label>
              <input
                type='password'
                id='password'
                placeholder='password_example'
              ></input>
            </div>
          </form>
          <NavLink
            to='/sunrise/dashboard'
            className={styles.link}
          >
            <button></button>
          </NavLink>
          <button className={styles.steam}>
            <p>  steam</p>
            <img src={steam} />
          </button>
        </div>
      </div>
      <div className={styles.right}>
        <img src={image} />
      </div>
    </div>
  )
}

export default Login
