import styles from "../styles/Footer.module.scss"

import lines from "../Images/bgLines.png"

import icon1 from "../Images/VK icon.svg"
import icon2 from "../Images/discord icon.svg"
import icon3 from "../Images/steam icon.svg"
import icon4 from "../Images/telegram icon.svg"
import icon5 from "../Images/yourube icon.svg"

import { NavLink } from "react-router-dom"

import ScrollToTop from "react-scroll-to-top"

function Footer() {
  return (
    <div className={styles.footer}>
      <div className={styles.wrapper}>
        <div className={styles.left}>
          <div className={styles.socials}>
            <ul>
              <li>
                <a href=''>
                  <img src={icon1} />
                </a>
              </li>
              <li>
                <a href=''>
                  <img src={icon2} />
                </a>
              </li>
              <li>
                <a href=''>
                  <img src={icon3} />
                </a>
              </li>
              <li>
                <a href=''>
                  <img src={icon4} />
                </a>
              </li>
              <li>
                <a href=''>
                  <img src={icon5} />
                </a>
              </li>
            </ul>
            <p> 2023 Sunrise    </p>
            <p>   RITONKIN</p>
          </div>
        </div>
        <div className={styles.right}>
          <NavLink to='/sunrise/project'> </NavLink>
          <NavLink to='/sunrise/team'></NavLink>
          <NavLink to='/sunrise/jobs'>  </NavLink>
          <NavLink to='/sunrise/events'></NavLink>
          <NavLink to='/sunrise/documents'></NavLink>
          <NavLink to='/sunrise/contacts'></NavLink>
        </div>
        <img
          src={lines}
          className={styles.line2}
        />
        <img
          src={lines}
          className={styles.line3}
        />
      </div>
    </div>
  )
}

export default Footer
