import { NavLink } from "react-router-dom"

import styles from "../styles/Header.module.scss"

import logo from "../Images/logo.png"
import account from "../Images/icon.png"
import burger from "../Images/burger.png"
import { useState } from "react"

import DashboardBtn from "../UI/dashboardBtn"

import iconPerson from "../Images/dashboard/icon-person.svg"
import metro from "../Images/dashboard/icon-metro.svg"
import trolleybus from "../Images/dashboard/trolleybus_white 1.svg"
import tram from "../Images/dashboard/material-symbols_tram.svg"
import help from "../Images/dashboard/bx_support.svg"
import exit from "../Images/dashboard/exit.svg"

function HeaderLk() {
  const [showBurger, setShowBurger] = useState(false)
  return (
    <div className={styles.header}>
      <NavLink
        className=''
        to='/sunrise'
      >
        <img src={logo}></img>
      </NavLink>
      <nav className={styles.nav}></nav>
      <button
        className={styles.burgerMenuButton}
        onClick={() => setShowBurger((prev) => !prev)}
      >
        <img src={burger}></img>
      </button>
      {showBurger && (
        <>
          <div
            className={styles.burgerMenuWrapper}
            onClick={() => setShowBurger(false)}
          ></div>
          <div className={styles.burgerMenu}>
            <nav>
              <NavLink
                className={styles.link}
                to='/sunrise/dashboard'
              >
                <DashboardBtn
                  img={iconPerson}
                  title=''
                />
              </NavLink>
              <NavLink
                className={styles.link}
                to=''
              >
                <DashboardBtn
                  img={metro}
                  title=''
                />
              </NavLink>
              <NavLink
                className={styles.link}
                to=''
              >
                <DashboardBtn
                  img={trolleybus}
                  title=''
                />
              </NavLink>
              <NavLink
                className={styles.link}
                to=''
              >
                <DashboardBtn
                  img={tram}
                  title=''
                />
              </NavLink>
              <NavLink
                className={styles.link}
                to=''
              >
                <DashboardBtn
                  img={help}
                  title=''
                />
              </NavLink>
              <NavLink
                className={styles.link}
                to='/sunrise/login'
              >
                <DashboardBtn
                  img={exit}
                  title=''
                />
              </NavLink>
            </nav>
          </div>
        </>
      )}
    </div>
  )
}

export default HeaderLk
