import { useState } from "react"

import styles from "../styles/Documents.module.scss"

import lines from "../Images/bgLines.png"

function Documents() {
  const [activity, setActivity] = useState(0)

  return <div className={styles.documents}>
    <img
        src={lines}
        className={styles.line}
      />
    <h1></h1>
    <div className={styles.tabs}>
      <p className={
        activity === 0 ? (styles.active) : (styles.inActive)} onClick={() => setActivity(0)}></p>
      <p className={
        activity === 1 ? (styles.active) : (styles.inActive)} onClick={() => setActivity(1)}></p>
      <p className={
        activity === 2 ? (styles.active) : (styles.inActive)} onClick={() => setActivity(2)}></p>
    </div>
    <div className={styles.main}>
      <div className={styles.column}>
        <button></button>
        <p></p>
        <p></p>
        <p></p>
      </div>
      <div className={styles.column2}>
        <button></button>
        <p></p>
        <p></p>
        <p></p>
      </div>
      <div className={styles.column}>
        <button> </button>
        <p></p>
        <p></p>
        <p></p>
      </div>
    </div>
  </div>
}

export default Documents
